$primary-500: #F2C91E;
$primary-600: #F2C91E;
$primary-700: #F2C91E;

@import "../node_modules/@picocss/pico/scss/pico.scss";


:root {
  --primary: #8e24aa;
}

h1,
h3 {
  font-weight: normal;
}

[role=button] {
  color: #000000;
}

/* Navigation */
nav {
  background-color: #F3F5FF;
}

nav ul a {
  color: hsl(205, 20%, 32%);
  padding-left: 20px;
  padding-right: 20px;
}

nav ul a:hover {
  text-decoration: underline;
}

/* Mobile nav */
details[role=list] {
  padding-top: 3px;
}

details[role=list] summary::after {
  display: none;
}

nav details[role=list] summary[role=link]+ul {
  margin-left: -140px;
}

@media (min-width: 769px) {
  details[role=list] {
    display: none;
  }
}

@media (max-width: 768px) {
  nav ul .desktop {
    display: none;
  }
}

/* Mobile images */
@media (min-width: 992px) {
  .phonepic {
    margin: 50px;
  }

  .phonepic img {
    float: right;
  }
}

/* Blog */
.blog-image {
  text-align: center;
}


/* news */
@media (min-width: 992px) {
  .newslist {
    grid-template-columns: 2fr 1fr;
  }
}
.news-sidecolumn {
  padding: 40px;

  h4, p {
    font-size: 80%;
    margin-bottom: 1em;
  }
}
.news-items {
  small {
    font-size: 70%;
  }
}


/* Footer */
footer {
  font-size: 10px;
  text-align: center;
}


